import React from "react";
import { NavLink } from "react-router-dom";
export default function Landing() {
  return (
    <div
      style={{
        width: "100vw",
        height: "10vh",
        position: "fixed",
        left: "0",
        top: "0",
        zIndex: "5000",
        background: "black",
        color: "white"
      }}
    >
      <div style="width:100%;height:0px;position:relative;padding-bottom:56.250%;">
        <iframe
          src="https://streamable.com/s/0uamq/omimzi"
          frameborder="0"
          width="100%"
          height="100%"
          allowfullscreen
          style="width:100%;height:100%;position:absolute;left:0px;top:0px;overflow:hidden;"
        ></iframe>
      </div>
      <div
        style={{
          background: "white",
          width: "100%",
          position: "absolute",
          bottom: "5%",
          left: "0"
        }}
      >
        <NavLink to="/web/games">
          <h1></h1>
        </NavLink>
      </div>
    </div>
  );
}
